.app-button {
    margin: 0.5rem;
    position: relative;
    font-size: 1rem;
    font-weight: 600;
    padding: 0.5rem;
    cursor: pointer;
    border: none;
    background: none;
    color: #9448a3;
    transition: all 0.25s;
    text-decoration: none;
  }
  
  .app-button:hover,
  .app-button:focus-visible,
  .app-button.active {
    color: black;
    outline: none;
  }
  
  .app-button::before,
  .app-button::after {
    content: "";
    width: 1rem;
    height: 100%;
    position: absolute;
    bottom: 0;
    left: 0;
    border: none;
    border-left: 1px solid;
    border-bottom: 1px solid;
    transition: all 0.25s;
  }
  
  .app-button:hover::before,
  .app-button:focus-visible::before {
    left: 0.25rem;
    bottom: 0.25rem;
    width: 100%;
    height: 100%;
  }
  
  .app-button::after {
    left: auto;
    bottom: auto;
    top: 0;
    right: 0;
    border: none;
    border-top: 1px solid;
    border-right: 1px solid;
  }
  
  .app-button.active::after {
      height: 1rem;
    }
  
  .app-button:hover::after,
  .app-button:focus-visible::after {
    right: 0.25rem;
    top: 0.25rem;
    width: 100%;
    height: 100%;
  }
  
  .app-button.active::before {
    width: 100%;
  }
  
  .app-button:hover::after {
    opacity: 1;
    border-left-width: 0.5rem;
  }
  
  @media (prefers-color-scheme: dark) {
    .app-button {
      color: #f2b3ff;
    }
  
    .app-button:hover,
    .app-button:focus-visible,
    .app-button.active {
      color: #fff;
    }
  }
  